<template>
	<nav>
		<v-app-bar
			app
			flat
		>
			<v-app-bar-nav-icon @click="drawer=!drawer"></v-app-bar-nav-icon>
			<v-app-bar-title class="grey--text text-uppercase">
				<span class="font-weight-light"></span>
				<span></span>
			</v-app-bar-title>
			<v-spacer></v-spacer>
			<v-dialog
				v-model="dialog"
				width="500"
				persistent
			>
				<template v-slot:activator="{ on, attrs }">
					<v-btn
						text
						v-bind="attrs"
						v-on="on"
						class="grey--text"
					></v-btn>

				</template>

				<v-data-table
					:headers="headers"
					:items="desserts"
					:items-per-page="5"
					disable-sort
					class="elevation-1"
				>
					<template v-slot:item.action="{ item }">
						<v-btn
							color="success"
							@click="chooseLesson(item)"
							class="cuifan mr-4"
							:disabled="item.disabled"
						>
							
						</v-btn>
						<v-btn
							color="error"
							@click="cancelselectedLesson(item)"
						>
							
						</v-btn>

					</template>

				</v-data-table>
				<div style="background-color:white">
					<span class="ml-10">{{classList.map(item=>item.cname)}}</span>
				</div>
				<div
					style="background-color:white"
					class="d-flex justify-center py-4"
				>

					<v-btn
						color="error"
						class="mr-4"
						@click="dialog = false"
					></v-btn>
					<v-btn
						@click="chooseCoursesRightly"
						color="success"
					></v-btn>
				</div>
			</v-dialog>

			<v-menu offset-y>
				<template v-slot:activator="{on,attrs}">
					<v-btn
						text
						color="grey"
						v-on="on"
						v-bind="attrs"
					>
						<v-icon left>mdi-menu</v-icon>
						<span class="text-uppercase"></span>
					</v-btn>
				</template>
				<v-list>
					<!-- todo: router -->
					<v-list-item
						v-for="link in links"
						:key="link.route"
						router
						:to="link.route"
					>
						<v-list-item-title>{{link.text}}</v-list-item-title>
					</v-list-item>
				</v-list>
			</v-menu>
			<v-btn
				text
				color="grey"
				@click="signOut"
			>
				<span></span>
				<v-icon right>mdi-arrow-right-thin-circle-outline</v-icon>
			</v-btn>
		</v-app-bar>
		<v-navigation-drawer
			v-model="drawer"
			app
			class="primary"
		>
			<v-row class="d-flex flex-column align-center">
				<v-dialog max-width="440">
					<template v-slot:activator="{on,attr}">
						<v-responsive class="mt-10">
							<v-avatar size="100">
								<img
									src="/images/avatar-1.jpg"
									class="avatar"
									title=""
									@click="updateAvatar"
									v-on="on"
									v-bind="attr"
								>
							</v-avatar>
						</v-responsive>
					</template>
					<v-card>
						<v-card-title></v-card-title>
						<v-card-text>
							<input
								type="file"
								accept="image/*"
								ref="input_img"
								@change="changeAvatar()"
							>
							<v-btn
								class="success white--text mx-0"
								@click="confirmChange"
							></v-btn>
						</v-card-text>
					</v-card>
				</v-dialog>
				<p class="white--text subtitle-1 mt-3">{{name}}</p>
				<popup />
			</v-row>
			<v-list>
				<v-list-item
					v-for="link in links"
					:key="link.route"
					router
					:to="link.route"
					class="d-flex justify-around"
				>
					<v-list-item-action class="ml-12">
						<v-icon class="white--text">{{link.icon}}</v-icon>
					</v-list-item-action>
					<v-list-item-content>
						<v-list-item-title class="white--text">{{link.text}}</v-list-item-title>
					</v-list-item-content>
				</v-list-item>
			</v-list>
		</v-navigation-drawer>
	</nav>
</template>

<script>
import Popup from './Popup.vue'
import { teacherData, studentData } from '@/utils/navData.js'
import {
	getTeacherInfo,
	getStudentInfo,
	uploadAvatar,
	getAvatar,
	getAllColleges,
	chooseTeacherClasses,
	getAllTeacherCanTeach,
} from '@/api/Info'
import { getUserAccount } from '@/api/Login'

export default {
	name: 'NavBar',
	components: {
		Popup,
	},
	data() {
		return {
			drawer: true, // 
			links: [], // 
			dialog: false, // 
			// attrs: true,
			// 
			name: '',
			coid: '', //

			// 
			nameRules: [
				(v) => !!v || '',
				(v) => /^[\u4e00-\u9fa5a-zA-Z0-9]+$/.test(v) || '',
				(v) => (v && v.length <= 30) || '',
			],
			courses: [], // 
			headers: [
				{
					text: '',
					align: 'start',
					sortable: false,
					value: 'cid',
				},
				{ text: '', value: 'cname' },
				{ text: '', value: 'action' },
			],
			desserts: [],
			classList: [], //
		}
	},
	created() {
		// / ,statuss t
		this.account = getUserAccount()
		let status = this.account[0]
		let requestsInfo = {
			s: getStudentInfo,
			t: getTeacherInfo,
		}
		requestsInfo[status]({
			[status + 'num']: this.account,
		})
			.then((res) => {
				this.coid = res.data.coid
				this.name = res.data[status + 'name']
				// 
				return getAllTeacherCanTeach({
					// todo: tnum
					coid: this.coid,
					tnum: this.account,
				})
			})
			.then((res) => {
				this.desserts = res.data
				if (this.account[0] === 's') return
				// 
				this.desserts.forEach((item) => {
					this.$set(item, 'disabled', false)
				})
			})

		// 
		let navLists = {
			t: teacherData,
			s: studentData,
		}
		navLists[status].forEach((nav) => {
			if (nav.name) {
				this.links.push({
					text: nav.meta.text,
					route: '/home/' + nav.name,
					icon: 'mdi-steam',
				})
			}
		})
	},

	methods: {
		// ,token,
		signOut() {
			window.localStorage.removeItem('token')
			window.location.reload()
			this.$message({
				type: 'success',
				message: '',
			})
		},

		updateAvatar() {
			// 
		},

		changeAvatar(data) {
			// 
			let img = this.$refs.input_img
			this.formData = new FormData()
			if (img.files[0]) {
				this.formData.append('qr_image', img.files[0])
			}
		},

		confirmChange() {
			// 
			uploadAvatar({
				num: this.account,
				file: this.formData,
			}).then(() => {})
		},

		// 
		chooseLesson(course) {
			// 
			this.desserts.forEach((item) => {
				if (course.cid === item.cid) return (item.disabled = true)
			})
			console.log(this.desserts)
			this.classList.push({
				cid: course.cid,
				cname: course.cname,
			})
			this.$message({
				type: 'success',
				message: '' + course.cname + '',
			})
		},

		cancelselectedLesson(course) {
			// 
			this.desserts.forEach((item) => {
				if (course.cid === item.cid) return (item.disabled = false)
			})
			this.classList.forEach((item, index) => {
				if (item.cid === course.cid) {
					this.classList.splice(index, 1)
				}
			})
		},

		// 
		chooseCoursesRightly() {
			chooseTeacherClasses({
				tnum: this.account,
				classList: this.classList,
			}).then((res) => {
				this.dialog = false
				this.$message({
					type: 'success',
					message: '',
				})
				window.location.reload()
			})
		},
	},
}
</script>

<style scoped>
.avatar {
	cursor: pointer;
}
</style>
