<template>
	<v-dialog
		max-width="440"
		class="ml-4"
		persistent
		v-model="dialog"
	>
		<template v-slot:activator="{on,attr}">
			<div>
				<v-btn
					class="success white--text text-uppercase mb-5 ml-1"
					v-on="on"
					v-bind="attr"
					text
				>
					
				</v-btn>
			</div>
		</template>
		<v-card>
			<v-card-title></v-card-title>
			<v-card-text>
				<v-form
					class="px-3"
					ref="form"
				>
					<v-text-field
						v-model="name"
						:rules="nameRules"
						outlined
						dense
						label=""
						required
					></v-text-field>
					<div>
						<v-row>
							<v-col cols="7">
								<v-radio-group
									v-model="sex"
									row
								>
									<span style="color:#666"></span>
									<v-radio
										label=""
										value="m"
									></v-radio>
									<v-radio
										label=""
										value="w"
									></v-radio>
								</v-radio-group>
							</v-col>
							<v-col cols="5">
								<v-select
									style="width:200px"
									:items="institutes"
									v-model="coid"
									label=""
									class="mt-3"
									dense
									outlined
									max-width="160"
								></v-select>
							</v-col>
						</v-row>
					</div>
					<v-text-field
						v-model="phone"
						:rules="phoneRules"
						:counter="11"
						label=""
						outlined
						dense
						required
					></v-text-field>
					<v-text-field
						v-model="email"
						:rules="emailRules"
						label=""
						required
						outlined
						dense
					></v-text-field>
					<v-row>
						<v-col>
							<v-btn
								class="info white--text mx-0"
								@click="dialog = false"
							></v-btn>
							<v-btn
								color="error"
								class=" white--text   mx-4"
								@click="reset"
							>
								
							</v-btn>
							<v-btn
								class="success white--text mx-0"
								@click="submit"
							></v-btn>
						</v-col>
					</v-row>
				</v-form>
			</v-card-text>
		</v-card>
	</v-dialog>
</template>

<script>
import {
	getTeacherInfo,
	getStudentInfo,
	updateTeacherInfo,
	updateStudentInfo,
} from '@/api/Info'
import { getUserAccount } from '@/api/Login'

export default {
	name: 'Popup',
	components: {},
	data() {
		return {
			// 
			account: '',
			name: '',
			sex: 'm',
			institutes: ['', '', ''],
			coid: '',
			phone: '',
			email: '',
			// 
			dialog: false,
			// 
			nameRules: [
				(v) => !!v || '',
				(v) => /^[\u4e00-\u9fa5a-zA-Z0-9]+$/.test(v) || '',
				(v) => (v && v.length <= 30) || '',
			],
			phoneRules: [
				(v) => !!v || '',
				(v) => /^\d+$/.test(v) || '',
				(v) => (v && v.length === 11) || '11',
			],
			emailRules: [
				(v) => !!v || '',
				(v) =>
					/^[1-9]\d{4,10}@qq\.com$/.test(v) || 'QQ',
			],
		}
	},

	created() {
		// /, flags,t
		this.account = getUserAccount()
		let flag = this.account[0]
		let requestsInfo = {
			s: getStudentInfo,
			t: getTeacherInfo,
		}
		requestsInfo[flag]({
			[flag + 'num']: this.account,
		}).then((res) => {
			this.sex = res.data[flag + 'sex']
			this.phone = res.data[flag + 'phone']
			this.name = res.data[flag + 'name']
			this.institute = ['', '', ''][res.data.coid]
			this.email = res.data.email
		})
	},

	methods: {
		// /
		submit() {
			if (!this.$refs.form.validate()) return
			let flag = this.account[0]
			let updateInfo = {
				s: updateStudentInfo,
				t: updateTeacherInfo,
			}
			updateInfo[flag]({
				coid: this.institutes.findIndex((institute) => institute === this.coid),
				email: this.email,
				[flag + 'num']: this.account,
				[flag + 'name']: this.name,
				[flag + 'phone']: this.phone,
				[flag + 'sex']: this.sex,
			}).then((res) => {
				if (res.data.state) {
					this.$message({
						type: 'success',
						message: res.data.msg,
					})
					this.dialog = false
					window.location.reload()
				}
			})
		},
		reset() {
			this.$refs.form.reset()
		},
	},
}
</script>
